        display: flex;
        -webkit-box-pack: justify;
        -ms-flex-pack: justify;
        justify-content: space-between;
        -webkit-box-align: center;
        -ms-flex-align: center;
        align-items: center;
        -ms-flex-wrap: wrap;
        flex-wrap: wrap;
        span {
          -webkit-box-flex: 1;
          -ms-flex: 1;
          flex: 1;
          padding-left: 20px;
          line-height: 1.6;
          vertical-align: middle;
